import React, {
    FC,
    forwardRef,
    useContext,
    useEffect,
    useImperativeHandle,
    useRef,
} from "react";
import { SelectionContext } from ".";
import styles from './index.module.css';

/**
 * @File: SelectItem
 * @Author: Ux
 * @Date: 2020/8/6
 * @Description:
 */

interface SelectItemProps {
    disabled?: boolean;
    selected?: () => void;
    unSelected?: () => void;
    children: any;
    setChild?: (ref: any) => ReturnType<typeof ref>;
}

/**
    style?: CSSProperties,
    disabled?: boolean,
    selected?: () => void,
    unSelected?:()=>void,
    children: any,
    setChild?: (ref: any) => ReturnType<typeof ref>
 * Item
 * refcontext
 * context
 * contextcontext
 */
/**
 * 
 * @param style 
 * @param disabled 
 * @param selected 
 * @param unSelected 
 * @param setChild (ref:any)=>  ReturnType<typeof ref> 
 */
const SelectItem: FC<SelectItemProps> = ({
    children,
    unSelected,
    setChild,
    selected,
    disabled,
}) => {
    const context = useContext(SelectionContext);
    const _this = Symbol("SelectItem");
    const ref = useRef<any>();
    useEffect(() => {
        if (disabled) {
            context.delete(_this);
        } else {
            context.set(_this, ref.current);
        }
        return () => {
            context.delete(_this);
        };
    }, [disabled, _this, context]);
    return (
        <Item
            ref={ref}
            setChild={setChild}
            selected={selected}
            unSelected={unSelected}
        >
            {children}
        </Item>
    );
};

export default SelectItem;

const Item = forwardRef((props: any, ref: any) => {
    let _this: any;
    useImperativeHandle(ref, () => {
        return {
            // antdclass
            // 
            select: false,
            // 
            element: {
                offsetWidth: _this.offsetWidth,
                offsetHeight: _this.offsetHeight,
                offsetTop: _this.offsetTop,
                offsetLeft: _this.offsetLeft,
            },
            // 
            beSelected() {
                props.selected && props.selected();
                // console.log('');
            },
            // 
            beUnSelected() {
                props.unSelected && props.unSelected();
                // console.log('');
            },
        };
    });

    // SelectItemoffset
    // react-windowreact-windowListGrid
    // 
    const setRef = (ref: any) => {
        return props.setChild ? props.setChild(ref) : ref;
    };

    return (
        <div ref={(ref) => (_this = setRef(ref))} className={styles.selectionItem}>
            {React.Children.only(props.children)}
        </div>
    );
});
