import React, { CSSProperties, FC, useContext, useEffect } from "react";
import { SelectionContext } from ".";
import styles from "./index.module.css";

/**
 * @File: SelectFrame
 * @Author: Ux
 * @Date: 2020/8/6
 * @Description:
 */
interface SelectFrameProps {
    positionLT: { left: number; top: number };
    size: { width: number; height: number };
    show: boolean;
    style?: CSSProperties;
    scrollOffset?: number;
}

const SelectFrame: FC<SelectFrameProps> = ({
    positionLT,
    size,
    style,
    show,
    scrollOffset,
}) => {
    // context
    const items = useContext(SelectionContext);
    const Istyle = {
        left: positionLT.left + "px",
        top: positionLT.top + "px",
        width: size.width + "px",
        height: size.height + "px",
    };

    /**
     * beSelected()
     * unselected
     * 
     */
    //*****//Issue react-window
    // react-window
    // react-windowreact-window
    // 
    // unselected
    const getSelectedItem = () => {
        items.forEach((v) => {
            const {
                offsetWidth,
                offsetHeight,
                offsetTop,
                offsetLeft,
            } = v.element;
            // 
            scrollOffset = scrollOffset || 0;
            const l = offsetWidth + offsetLeft;
            const t = offsetHeight + offsetTop;
            if (
                l > positionLT.left &&
                t > positionLT.top + scrollOffset && // 
                offsetLeft < positionLT.left + size.width &&
                offsetTop < positionLT.top + size.height + scrollOffset // 
            ) {
                if (!v.select) {
                    v.beSelected();
                }
                v.select = true;
            } else {
                if (v.select) {
                    v.beUnSelected();
                }
                v.select = false;
            }
        });
    };

    // 
    useEffect(() => {
        if (size && size.width && size.height) getSelectedItem();
    }, [size]);

    return show ? <div style={Istyle} className={styles.frame} /> : <></>;
};

export default React.memo(SelectFrame, (pre, next) => {
    if (
        pre.size.height === next.size.height &&
        pre.size.width === next.size.width
    ) {
        return true;
    }
    return false;
});
